.btn{
    font-size: inherit;
    background: none;
    font-weight: bold;
    border: none;
    outline: none;
    border-radius: 5px;
    padding: 5px 20px;
    cursor: pointer;
}

.btn--primary{
    color:white;
    background-color: hsl(200,100%,50%);
}
.btn--primary:hover{
    background-color: hsl(200,100%,35%);
}


.btn--danger{
    color:white;
    background-color: hsl(0,100%,50%);
}
.btn--danger:hover{
    background-color: hsl(0,100%,35%);
}


